:root {
  --font-body: Arial, sans-serif;
  --font-title: "Cyberthrone", italic;
  --transition-min: .2s;
  --transition-max: 8s;
}

:root {
  --white: hsl(0, 0%, 100%);
  --aqua: hsl(179, 100%, 50%);
  --cherry: hsl(327, 88%, 37%);
  --gray: hsl(0, 0%, 20%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: rem(30);
  font-family: var(--font-title);
  color: var(--white);
  overflow: hidden;
}

#click {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  cursor: pointer;
  background-color: var(--black);
}
#click::after {
  content: "";
  width: 599px;
  height: 336px;
  background: url("../images/loading.gif") no-repeat 50%/contain;
}

#model {
  position: absolute;
  top: 18%;
  left: 6%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 1;
}

.hidden {
  color: var(--white);
}

:root {
  --animate-delay: 1s;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.delay-1 {
  animation-delay: 1s;
  animation-delay: var(--animate-delay);
}

.delay-2 {
  animation-delay: calc(1s * 1.2);
  animation-delay: calc(var(--animate-delay) * 1.2);
}

.delay-3 {
  animation-delay: calc(1s * 1.3);
  animation-delay: calc(var(--animate-delay) * 1.3);
}

.delay-4 {
  animation-delay: calc(1s * 1.4);
  animation-delay: calc(var(--animate-delay) * 1.4);
}

.delay-5 {
  animation-delay: calc(1s * 1.5);
  animation-delay: calc(var(--animate-delay) * 1.5);
}

.delay-6 {
  animation-delay: calc(1s * 1.6);
  animation-delay: calc(var(--animate-delay) * 1.6);
}

.delay-7 {
  animation-delay: calc(1s * 1.7);
  animation-delay: calc(var(--animate-delay) * 1.7);
}

.delay-8 {
  animation-delay: calc(1s * 1.8);
  animation-delay: calc(var(--animate-delay) * 1.8);
}

.delay-9 {
  animation-delay: calc(1s * 1.9);
  animation-delay: calc(var(--animate-delay) * 1.9);
}

.delay-10 {
  animation-delay: calc(1s * 2);
  animation-delay: calc(var(--animate-delay) * 2);
}

.delay-11 {
  animation-delay: calc(1s * 2.1);
  animation-delay: calc(var(--animate-delay) * 2.1);
}

.delay-12 {
  animation-delay: calc(1s * 2.2);
  animation-delay: calc(var(--animate-delay) * 2.2);
}

.delay-13 {
  animation-delay: calc(1s * 2.3);
  animation-delay: calc(var(--animate-delay) * 2.3);
}

.delay-14 {
  animation-delay: calc(1s * 2.4);
  animation-delay: calc(var(--animate-delay) * 2.4);
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
h1,
h2,
h3,
ul,
p {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}

li {
  list-style: none;
  cursor: pointer;
}
li:hover {
  text-decoration: underline;
}

main li {
  color: var(--aqua);
}

nav ul {
  display: flex;
}

nav ul li {
  color: var(--cherry);
  margin: 0 10px;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

main a {
  color: var(--aqua);
}

.wb-body {
  background-color: var(--gray);
  padding: 20px;
}

.wrapper {
  display: flex;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
}
.wrapper__outer {
  width: 100vh;
  height: 100vw;
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-x: scroll;
}
.wrapper__outer::-webkit-scrollbar {
  display: none;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  padding: 0 1.25rem;
}
.container__player {
  position: relative;
  bottom: -2.5rem;
}
.container__portfolio {
  max-width: 71.25rem;
}

.section__scroll {
  overflow: auto;
}
.section__scroll::-webkit-scrollbar {
  display: none;
}

.slide {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-image: url("../images/background.gif");
}
.slide.one {
  align-items: center;
  justify-content: center;
}
.slide.two {
  align-items: flex-end;
  background-image: none;
  background-color: var(--gray);
  box-shadow: -140px -40px 40px #141414;
  height: 200vh;
}
.slide::before {
  content: "";
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}

.title__big {
  font-size: 5rem;
  margin-top: 0.9375rem;
  position: relative;
  transform: translate(0%, 0%);
}
.title__big:before, .title__big:after {
  content: "codlipa";
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.8;
}
.title__big:after {
  color: var(--cherry);
  z-index: -2;
}
.title__big:before {
  color: var(--aqua);
  z-index: -1;
}
.title__big:hover:before {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.title__big:hover:after {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite reverse;
}
.title__medium {
  font-size: 3.75rem;
  font-weight: 700;
  margin: 0.9375rem 0 0.3125rem;
  background-image: linear-gradient(var(--cherry), var(--aqua));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-0.3125rem, 0.3125rem);
  }
  40% {
    transform: translate(-0.3125rem, -0.3125rem);
  }
  60% {
    transform: translate(0.3125rem, 0.3125rem);
  }
  80% {
    transform: translate(0.3125rem, -0.3125rem);
  }
  100% {
    transform: translate(0);
  }
}
.player {
  display: flex;
  align-items: flex-end;
  height: 100vh;
}
.player__logo {
  position: absolute;
  bottom: 74.5%;
  left: 16.5%;
  z-index: 1;
}
.player__console {
  width: 46.125rem;
  height: 23.25rem;
}
.player__game {
  position: absolute;
  top: 11.2%;
  left: 17.8%;
  width: 31.375rem;
  height: 18.125rem;
}

svg:nth-child(1) {
  top: -30%;
  left: -45%;
  width: 31.25rem;
  position: absolute;
  animation: rotation var(--transition-max) infinite linear;
}

svg:nth-child(2) {
  top: 27%;
  left: -50%;
  width: 25rem;
  position: absolute;
  animation: rotation var(--transition-max) infinite linear;
}

svg:nth-child(3) {
  top: 70%;
  left: -70%;
  width: 37.5rem;
  position: absolute;
  animation: rotation var(--transition-max) infinite linear;
}

text {
  font-size: 2rem;
  fill: var(--white);
}

@keyframes rotation {
  from {
    transfrom: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.portfolio {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
  height: 100vh;
}
.portfolio__link {
  width: 23.75rem;
  height: 15.625rem;
  overflow: hidden;
}
.portfolio__image {
  background-position: 50%;
  background-size: cover;
  width: 100%;
  height: 100%;
  transition: var(--transition-max);
}
.portfolio__image:hover {
  transform: scale(1.2);
}
.portfolio__image.item-soon {
  background-image: url("https://via.placeholder.com/380x250?text=Item+soon");
}
.portfolio__image.item-1 {
  background-image: url("../images/content/item-1.webp"), url("../images/content/item-1.jpg");
}
.portfolio__image.item-2 {
  background-image: url("../images/content/item-2.webp"), url("../images/content/item-2.jpg");
}
.portfolio__image.item-3 {
  background-image: url("../images/content/item-3.webp"), url("../images/content/item-3.jpg");
}
.portfolio__image.item-4 {
  background-image: url("../images/content/item-4.webp"), url("../images/content/item-4.jpg");
}
.portfolio__image.item-5 {
  background-image: url("../images/content/item-5.webp"), url("../images/content/item-5.jpg");
}
.portfolio__image.item-6 {
  background-image: url("../images/content/item-6.webp"), url("../images/content/item-6.jpg");
}

@font-face {
  font-family: var(--font-title);
  src: url("../fonts/Cyberthrone.woff2") format("woff2"), url("../fonts/Cyberthrone.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}